@tailwind components;
@tailwind utilities;

body {
  margin: 0;
}
@import url("https://fonts.googleapis.com/css2?family=Josefin+Sans:wght@200&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Bebas+Neue&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@200&display=swap");
body {
  margin: 0;
}
* {
  margin: 0;
  padding: 0;
}
main {
  font-family: "Poppins", sans-serif;
  min-height: 100vh;
  background: #7f7fd5; /* fallback for old browsers */
  background: -webkit-linear-gradient(
    to right,
    #91eae4,
    #86a8e7,
    #7f7fd5
  ); /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(
    to right,
    #91eae4,
    #86a8e7,
    #7f7fd5
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */  display: flex;
  align-items: center;
  justify-content: center;
  padding: 2%;

}

.rsvp {
  background: white;
  min-height: calc(100vh - 155px);
  width: 80%;
  background: linear-gradient(
    to right bottom,
    rgba(255, 255, 255, 0.5),
    rgba(255, 255, 255, 0.3)
  );
  z-index: 2;
  backdrop-filter: blur(2rem);
  display: flex;
  border: 2em solid #7f7fd5;
  border-bottom: 0em;
  border-left: 0em;
}
@media only screen and (max-width: 720px) {
  .test {
    display: none;
}
.rsvp form{
  padding-right: 10%;

}}

.rsvp form {
  padding-left: 10%;

  padding-top: 7%;
  padding-bottom: 10%;

}

.form-element {
  display: flex;
  flex-direction: column;
  margin-bottom: 30px;
}

.form-element span {
  margin-bottom: 10px;
}

input {
  border: 1px solid blue;
  line-height: 2;
  padding: 5px;
  border-radius: 5px;
  width: 100%;

}


form button {
  width: 50%;
  padding: 20px;
  color: white;
  background: purple;
  border-radius: 5px;
  border: 0;
  font-family: "fredoka";
  font-style: normal;
  font-weight: 400;;
}



textarea{
  border: 1px solid blue;
  border-radius: 5px;
  width: 100%;
  padding: 10px;
}

.wanna{
  font-family: "fredoka";
  font-style: normal;
  font-weight: 400;
  font-size: 2em;
  display: flex;
  align-items: center;
  text-align: center;
  color: #000080;
  margin-top: 50%;
  padding-left: 10%;
}
@import url('https://fonts.googleapis.com/css2?family=Fredoka:wght@500&display=swap');
