@import url("https://fonts.googleapis.com/css2?family=Oswald&display=swap");
.App {
  text-align: center;
}

.App-logo {
  height: 12em;
  width: 10em;
  pointer-events: none;
  border-radius: 3em 3em 0 3em;
  border: 1em solid #7f7fd5;
  border-top: 0em;
  margin: 1em;
  transition: visibility 0s linear 0.23s, opacity 0.23s linear;
  border-left: 0em;
}
.App {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.head {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 400;
  font-size: 2em;
  display: flex;
  align-items: center;
  text-align: center;
  color: #000080;
  margin: 0.2em;
  overflow: hidden;
  border-right: 0.12em solid #97d57f;
  white-space: nowrap;
  margin: 0 auto;
  animation: typing 3.5s steps(40, end), blink-caret 0.75s step-end infinite;
}
.ptag {
  font-family: "Oswald";
}

@keyframes typing {
  from {
    width: 0;
  }
  to {
    width: 22%;
  }
}

@keyframes blink-caret {
  from,
  to {
    border-color: transparent;
  }
  50% {
    border-color: #91eae4;
  }
}

.App {
  background: #7f7fd5; /* fallback for old browsers */
  background: -webkit-linear-gradient(
    to right,
    #91eae4,
    #86a8e7,
    #7f7fd5
  ); /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(
    to right,
    #91eae4,
    #86a8e7,
    #7f7fd5
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
  width: 100%;
}

.imgs {
  perspective: 800px;
}
.App-logo {
  animation: fadeIn linear 4s;
  -webkit-animation: fadeIn linear 4s;
  -moz-animation: fadeIn linear 4s;
  -o-animation: fadeIn linear 4s;
  -ms-animation: fadeIn linear 4s;
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@-moz-keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@-webkit-keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@-o-keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@-ms-keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.h {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 400;
}
